/* Top */
.roulette-pro-regular-design-top {
  position: absolute;
  z-index: 10;
  width: 4px;
  height: 100%;
  background: #ff7524 linear-gradient(0deg, #ef3a28, #ff9305);
  border-radius: 2px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.50);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
