/* Prize item */
.roulette-pro-regular-prize-item {
  text-align: center;
}

.roulette-pro-regular-prize-item:first-child .roulette-pro-regular-prize-item-wrapper {
  border-left: none;
}

.roulette-pro-regular-prize-item-wrapper {
  position: relative;
  z-index: 2;
  width: 100%;
